  async getBlockNumberFromHash(hash) {
    const { api } = this.state;
    if (api.type) {
      const { rpc } = api;
      const { chain } = rpc;
      const blockData = await chain.getBlock(hash);
      return parseInt(blockData.block.header.number.toString());
    }
    return 0;
  }

  async getHashFromNumber(number) {
    const { api } = this.state;
    if (api.type) {
      const { rpc } = api;
      const { chain } = rpc;
      const blockHash = await chain.getBlockHash(number);
      return blockHash;
    }
    return 0;
  }

  async getBlockFromHash(hash) {
    const { api } = this.state;
    if (api.type) {
      const { rpc } = api;
      const { chain } = rpc;
      const blockData = await chain.getBlock(hash);
      const test = blockData.block.extrincs;
      return blockData;
    }
    return 0;
  }

  async getBlockHeaderFromHash(hash) {
    const { api } = this.state;
    if (api.type) {
      const { rpc } = api;
      const { chain } = rpc;
      const blockData = await chain.getHeader(hash);
      return blockData;
    }
    return 0;
  }

  async getTimeFromHash(hash) {
    let temp;
    const { api, lastBlock, lastFinalizedBlock } = this.state;
    if (api.type) {
      const deriveChain = api.derive.chain;
      const rpcChain = api.rpc.chain;
      const { block } = await rpcChain.getBlock(hash);
      // const block = await api.query.getBlock(hash);
      const timestamp = api.query.timestamp;
      const didUpdate = await timestamp.didUpdate();
      const blockchainTimestamp = await timestamp.now(); // Try subtracting todays date from this date?
      let timeDifference;
      const [parsedBlockchainTimestamp] = blockchainTimestamp.words;
      const blockchainTime = new Date(parsedBlockchainTimestamp);
      const currentTime = new Date();
      const diff1 = currentTime - parsedBlockchainTimestamp;
      const diff2 = parsedBlockchainTimestamp - currentTime;
      const diff3 =
        currentTime -
        blockchainTimestamp.words[0] * blockchainTimestamp.words[1];
      // TODO: Fix timestamp here.
      const time1 = new Date(diff1);
      const time2 = new Date(diff2);
      const time3 = new Date(diff3);
      return time1;
    }
  }

  async getEventsFromHash(hash) {
    const { api } = this.state;
    if (api.type) {
      const { query } = api;
      const { system } = query;
      const { events } = system;
      // const {
      //   at,
      //   creator,
      //   hash,
      //   is,
      //   key,
      //   meta,
      //   method,
      //   multi,
      //   range,
      //   size,
      //   sizeAt,
      //   toJson,
      // } = events;
      const event = await events.at(hash);
      return event;
    }
    return {};
  }

  async parseEventDataAlt(events) {
    const hash = events.createdAtHash;
    const blockHeader = await this.getBlockHeaderFromHash(hash);
    const blockNumber = await this.getBlockNumberFromHash(hash);
    const blockTime = await this.getTimeFromHash(hash);
    const eventData = {
      blockNumber,
      children: [],
      timestamp: blockTime.toISOString(),
    };
    for (const item of events) {
      const { data, index, meta, method, section } = item.get('event'); // Missing size
      const { phase } = item;
      const [readable] = data.toHuman();
      const metadata = data.meta;
      const { args, docs, isEmpty, name, registry, Type } = meta; // Missing size
      const [eventId] = phase.asApplyExtrinsic.words;
      const author = await blockHeader.author;
      eventData.children.push({
        arguments: args.toHuman(),
        // blockAuthor: blockHeader.author,
        eventId: `${blockNumber}-${eventId}`,
        event: `${section}.${method}`,
      });
    }

    const test = events
      .filter(
        (test) => {
          const { phase } = test;
          // console.log(phase.asApplyExtrinsic);
          phase.isApplyExtrinsic;
          // phase.isApplyExtrinsic && phase.asApplyExtrinsic.eq();
        },
        // phase.isApplyExtrinsic && phase.asApplyExtrinsic.eq(index), // Need "index" from forEach
      )
      .map((test) => {
        const { event } = test;
        return `${event.section}.${event.method}`;
      });
    return [eventData];
  }

