const mapStateToProps = (state) => {
  const { api, blockchain } = state.api;
  const { percentLoaded } = state.loading;
  const { block, currency, endpoint } = state.polkadot;
  const { columns, rows } = state.table;
  return { api, columns, rows };
};

// These come in as "props" to the component.
const mapDispatchToProps = (dispatch) => {
  // Are these actually state because there's multiple & they constantly change?
  // setCurrentBlock
  // setLastBlock
  // setLastFinalizedBlock
  // setBlockHash
  // setBlockNumber
  return {
    addColumns: (data) => {
      dispatch(addColumns(data));
    },
    addRow: (data) => {
      dispatch(addRow(data));
    },
    addRows: (data) => {
      dispatch(addRows(data));
    },
    clearTable: () => {
      dispatch(clearTable());
    },
    setColumns: (data) => {
      dispatch(setColumns(data));
    },
    setCurrency: (data) => {
      dispatch(setCurrency(data));
    },
    setGenesisBlock: (data) => {
      dispatch(setGenesisBlock(data));
    },
    setPercentLoaded: (data) => {
      dispatch(setPercentLoaded(data));
    },
  };
};

// export default DataInput;

export default connect(mapStateToProps, mapDispatchToProps)(DataInput);
