const mapStateToProps = (state) => {
  const { loading, polkadot, table } = state;
  const { percentLoaded } = loading;
  return {
    columns: table.columns,
    loading: percentLoaded > 0 ? true : false,
    percentLoaded: percentLoaded,
    rows: table.rows,
  };
};

