import { tableActions } from '../actions';

const { ADD_COLUMNS, ADD_ROW, ADD_ROWS, CLEAR_TABLE, SET_COLUMNS } =
  tableActions.ACTION_TYPES;

// Reducers are just functions that return a state. These functions are passed 2 arguments: + initial state & + the action associated with this payload
const columns = (state = { columns: [] }, action) => {
  // We read the action and then return the appropriate state. (THIS IS WHERE WE MODIFY STATE)
  switch (action.type) {
    case ADD_COLUMNS:
      return [...state, ...action.payload];
    case SET_COLUMNS:
      return action.payload;
    default:
      return state; // We typically just return the original state if none of the action types match.
  }
};

let rowId = 0;

const rows = (state = { rows: [] }, action) => {
  switch (action.type) {
    case ADD_ROW:
      const key = `${++rowId}`;
      // console.log(`key = ${key}`);
      // console.log(action.payload);
      action.payload.key = key;
      return [...state, action.payload];
    case ADD_ROWS:
      return [...state, action.payload];
    case CLEAR_TABLE:
      return state;
    default:
      return state;
  }
};

export default {
  columns,
  rows,
};
