import { loadingActions } from '../actions';

const { GET_PERCENT_LOADED, SET_PERCENT_LOADED } = loadingActions.ACTION_TYPES;

const percentLoaded = (state = { percentLoaded: 1 }, action) => {
  switch (action.type) {
    case SET_PERCENT_LOADED:
      return action.payload;
    case GET_PERCENT_LOADED:
      return state;
    default:
      return state;
  }
};

export default { percentLoaded };
