// Websockets Article - https://blog.logrocket.com/websockets-tutorial-how-to-go-real-time-with-node-and-react-8e4693fbf843/

class App extends Component {
  constructor(props) {
    super(props);
    const { api, endpoint, loading, percentLoaded } = props;
    this.state = { api, endpoint, loading, percentLoaded };
    // this.setState({ api, endpoint, percentLoaded });
    // this.initializeProvider = this.initializeProvider.bind(this);
  }

  componentDidMount() {
    const test = this.props.setApi('wss://rpc.polkadot.io');
    const { api } = this.state;
    if (api.type) {
      const { query } = api;
      const { system } = query;
      this.setState({
        loading: false,
        percentLoaded: 0,
      });
    }
  }

  // async initializeProvider() {
  //   const { endpoint } = this.state;
  //   const provider = new WsProvider(endpoint);
  //   const api = await ApiPromise.create({ provider });
  //   this.setState({ percentLoaded: 75 });
  //   this.props.setApi(endpoint);
  // }

  componentDidUpdate() {
    // console.log(
    //   "This maybe the trick to setting the API to state. However, it's probably better to just stay as props?",
    // );
  }

  async debug(obj) {
    const { api } = this.state;
    const { derive, query, rpc } = api;

    const chainProperties = await api.registry.getChainProperties();

    const systemNumber = await api.query.system.number();
    const raw = await rpc.system.chain.raw();
    const [lastBlockNumber] = systemNumber.words;
    const lastBlockHash = await this.getHashFromNumber(lastBlockNumber);

    const header = await rpc.chain.getHeader();

    const test = await rpc.chain.getFinalizedHead();
    const temp = await rpc.chain.getBlock(lastBlockHash);
    const blockHeader = await temp.block.header;

    // // no blockHash is specified, so we retrieve the latest
    const eventRecords = await this.getEventsFromHash(temp.block.header.hash);
    const events = await this.parseEventData(eventRecords);

    const digest = await blockHeader.digest;
    const logs = await digest.logs;
    const magicNumber = await api.runtimeMetadata.magicNumber;

    await temp.block.extrinsics.forEach((ex, index) => {
      // ({ method: { method, section } }, index) => {
      // the extrinsics are decoded by the API, human-like view

      const {
        isSigned,
        meta,
        method: { args, method, section },
      } = ex;

      // filter the specific events based on the phase and then the
      // index of our extrinsic in the block

      console.log(`${section}.${method}:: ${events || 'no events'}`);
      // console.log(`${section}.${method}:: ${events.join(', ') || 'no events'}`);

      // this.printDocumentation(meta);

      // explicit display of name, args & documentation
      console.log(
        `Function:\n${section}.${method}(${args
          .map((a) => a.toString())
          .join(', ')})`,
      );

      // signer/nonce info
      if (isSigned) {
        console.log(
          `signer=${ex.signer.toString()}, nonce=${ex.nonce.toString()}`,
        );
      }
    });
  }

  render() {
    const { loading, percentLoaded } = this.state;
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <Layout className="site-layout">
          <AppHeader className="site-layout-background" />
          <Content style={{ margin: '0 1rem' }}>
            {loading ? (
              <Loader percent={percentLoaded} status="active" />
            ) : (
              <div
                className="site-layout-background"
                style={{ padding: 24, minHeight: 360 }}>
                <DataInput />
                <DataDisplay />
              </div>
            )}
          </Content>
          <AppFooter style={{ textAlign: 'center' }} />
        </Layout>
      </Layout>
    );
  }
}

