const mapStateToProps = (state) => {
  const { api, blockchain } = state.api;
  const { percentLoaded } = state.loading;
  const { block, currency, endpoint } = state.polkadot;
  const { columns, rows } = state.table;
  return {
    api,
    blockchain,
    block,
    currency,
    endpoint,
    loading: percentLoaded > 0 ? true : false,
    columns,
    rows,
    endpoint,
    percentLoaded,
  };
};

// These come in as "props" to the component.
const mapDispatchToProps = (dispatch) => {
  return {
    getApi: () => {
      dispatch(getApi());
    },
    setApi: (data) => {
      dispatch(setApi(data));
    },
    getEndpoint: () => {
      dispatch(getEndpoint());
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
