export const getBlockchain = () => {
  return (dispatch) => {
    dispatch({
      type: GET_BLOCKCHAIN,
    });
  };
};

export const setBlockchain = (payload) => {
  return (dispatch) => {
    dispatch({
      type: SET_BLOCKCHAIN,
      payload,
    });
  };
};

export const getEndpoint = () => {
  return (dispatch) => {
    dispatch({
      type: GET_ENDPOINT,
    });
  };
};

export const setEndpoint = (payload) => {
  return (dispatch) => {
    dispatch({
      type: SET_ENDPOINT,
      payload,
    });
  };
};

export const tempFilter = (filter) => ({
  type: 'TEMP_FILTER',
  filter,
});

// Action Constants
export const ACTION_TYPES = {
  API_ERROR,
  GET_API,
  SET_API,
  SET_BLOCKCHAIN,
  GET_BLOCKCHAIN,
  SET_ENDPOINT,
  GET_ENDPOINT,
};
