import { apiActions } from '../actions';

const {
  API_ERROR,
  GET_API,
  SET_API,
  SET_BLOCKCHAIN,
  GET_BLOCKCHAIN,
  SET_ENDPOINT,
  GET_ENDPOINT,
} = apiActions.ACTION_TYPES;

const api = (state = {}, action) => {
  switch (action.type) {
    case API_ERROR:
      return state;
    case GET_API:
      return state;
    case SET_API:
      return action.payload; // Might want to return pending promise so we can resolve it any time we want to re-access it.
    default:
      return state;
  }
};

const blockchain = (state = { blockchain: 'Polkadot' }, action) => {
  switch (action.type) {
    case GET_BLOCKCHAIN:
      return state;
    case SET_BLOCKCHAIN:
      return action.payload;
    default:
      return state;
  }
};

const endpoint = (state = { endpoint: 'wss://rpc.polkadot.io' }, action) => {
  switch (action.type) {
    case GET_ENDPOINT:
      return state;
    case SET_ENDPOINT:
      return action.payload;
    default:
      return state;
  }
};

export default {
  api,
  blockchain,
  endpoint,
};
