import { applyMiddleware, createStore, combineReducers } from 'redux';
import thunk from 'redux-thunk';

import reducers from './reducers';
// import { apiReducers, loadingReducers, polkadotReducers, tableReducers } from './reducers';

import apiReducers from './reducers/apiReducers';
// console.log(apiReducers);
const combinedApiReducers = combineReducers(apiReducers);

import loadingReducers from './reducers/loadingReducers';
// console.log(loadingReducers);
const combinedLoadingReducers = combineReducers(loadingReducers);

import polkadotReducers from './reducers/polkadotReducers';
// console.log(polkadotReducers);
const combinedPolkadotReducers = combineReducers(polkadotReducers);

import tableReducer from './reducers/tableReducers';
// console.log(tableReducer);
const combinedTableReducers = combineReducers(tableReducer);

const allReducersCombined = {
  api: combinedApiReducers,
  loading: combinedLoadingReducers,
  polkadot: combinedPolkadotReducers,
  table: combinedTableReducers,
};

const initialState = {
  loading: { percentLoaded: 0 },
  polkadot: {
    block: {},
    currency: 'DOT',
    // currentBlock: 0,
    // endBlock: 0,
    endpoint: 'wss://rpc.polkadot.io',
    // lastBlock: 0,
    // startBlock: 0,
  },
  table: {
    columns: [],
    rows: [],
  },
};

// const combinedReducers = combineReducers(reducers);

export function configureStore(initialState = {}) {
  const combinedReducers = combineReducers(reducers);
  // const store = createStore(reducers, initialState, applyMiddleware(thunk));
  // const store = createStore(
  //   combinedReducers,
  //   initialState,
  //   applyMiddleware(thunk),
  // );

  const store = createStore(
    combineReducers(allReducersCombined),
    initialState,
    applyMiddleware(thunk),
  );
  return store;
}

const store = configureStore(initialState);

// You can use subscribe() to update the UI in response to state changes.
