// Action Types
const ADD_COLUMNS = 'ADD_COLUMN';
const ADD_ROW = 'ADD_ROW';
const ADD_ROWS = 'ADD_ROWS';
const CLEAR_TABLE = 'CLEAR_TABLE';
const SET_COLUMNS = 'SET_COLUMNS';

