// // These are functions that will create and then dispatch an action.
export const addColumns = (payload) => {
  return (dispatch) => {
    dispatch({
      type: ADD_COLUMNS,
      payload,
    });
  };
};

export const setColumns = (payload) => {
  return (dispatch) => {
    dispatch({
      type: SET_COLUMNS,
      payload,
    });
  };
};

export const addRow = (payload) => {
  return (dispatch) => {
    dispatch({
      type: ADD_ROW,
      payload,
    });
  };
};

export const addRows = (payload) => {
  return (dispatch) => {
    dispatch({
      type: ADD_ROWS,
      payload,
    });
  };
};

export const clearTable = () => {
  return (dispatch) => {
    dispatch({
      type: CLEAR_TABLE,
    });
  };
};

// Action Filters
export const setColumnFilter = (filter) => ({
  type: 'SET_COLUMN_FILTER',
  filter,
});

export const setRowFilter = (filter) => ({
  type: 'SET_ROW_FILTER',
  filter,
});

// Action Constants
export const ACTION_TYPES = {
  ADD_COLUMNS,
  ADD_ROW,
  ADD_ROWS,
  CLEAR_TABLE,
  SET_COLUMNS,
};
