import { combineReducers } from 'redux';

// import * as apiReducers from './apiReducers';
// import * as formReducers from './formReducers';
import * as loadingReducers from './loadingReducers';
import * as polkadotReducers from './polkadotReducers';
import * as tableReducers from './tableReducers';

// import loadingReducers from './loadingReducers';
// import polkadotReducers from './polkadotReducers';
// import tableReducers from './tableReducers';

// https://redux.js.org/tutorials/fundamentals/part-3-state-actions-reducers

const reducers = {
  loadingReducers,
  tableReducers,
  polkadotReducers,
};

const combinedReducers = () => {
  combineReducers(reducers);
};

export default combinedReducers;
